*{
  margin:0;
  
  margin-left:auto;
  margin-right:auto;
}
.App {
  text-align: center;
  background-color: #282c34;
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  
}
.card{
  width:100px;
  height:100px;
  background:white;
}
nav{
  position:absolute;
  top:0;
  width:100%;
  background:white;
  display:flex;
  justify-content: center;
  align-items: center;

}
.link{         
  text-decoration: none;
  background:rgb(194, 194, 194);
  color:black;
  padding:20px;

}
.link:hover{
    background:rgb(230, 230, 230);
}
.home__container{
  position:absolute;
  width:100%;
  height:100%;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.home__container > * {
  margin:10px;
  width: 200px;
}